"use client"

import { motion, useInView } from "framer-motion"
import { useRef } from "react"
import { GlassCard } from "@/components/glass-card"
import { SectionWrapper } from "@/components/section-wrapper"
import Image from "next/image"

const skillCategories = [
  {
    title: "Languages",
    skills: [
      { name: "Python", proficiency: 95, logo: "/icons/python.png" },
      { name: "TypeScript", proficiency: 90, logo: "/typescript-programming-language-logo.jpg" },
      { name: "Go", proficiency: 80, logo: "/go-golang-programming-language-logo.jpg" },
      { name: "Java", proficiency: 75, logo: "/java-logo.png" },
    ],
  },
  {
    title: "Frameworks & Libraries",
    skills: [
      { name: "React", proficiency: 92, logo: "/react-javascript-library-logo.jpg" },
      { name: "PyTorch", proficiency: 88, logo: "/pytorch-deep-learning-logo.jpg" },
      { name: "Next.js", proficiency: 85, logo: "/nextjs-framework-logo.jpg" },
      { name: "TensorFlow", proficiency: 82, logo: "/tensorflow-machine-learning-logo.jpg" },
    ],
  },
  {
    title: "Tools & Platforms",
    skills: [
      { name: "Docker", proficiency: 90, logo: "/docker-container-logo.jpg" },
      { name: "AWS", proficiency: 85, logo: "/aws-amazon-web-services-logo.jpg" },
      { name: "Kubernetes", proficiency: 78, logo: "/kubernetes-container-orchestration-logo.jpg" },
      { name: "Git", proficiency: 95, logo: "/git-version-control-logo.jpg" },
    ],
  },
]

function SkillBar({ skill, delay }: { skill: { name: string; proficiency: number; logo: string }; delay: number }) {
  const ref = useRef(null)
  const isInView = useInView(ref, { amount: 0.5 })

  return (
    <motion.div ref={ref} className="space-y-2" whileHover={{ scale: 1.02 }} transition={{ duration: 0.2 }}>
      <div className="flex items-center justify-between">
        <div className="flex items-center gap-3">
          <div className="w-6 h-6 rounded bg-white/5 p-0.5 flex items-center justify-center">
            <Image
              src={skill.logo || "/placeholder.svg"}
              alt={`${skill.name} logo`}
              width={20}
              height={20}
              className="object-contain"
            />
          </div>
          <span className="font-mono text-sm text-white">{skill.name}</span>
        </div>
        <span className="font-mono text-sm text-cyan-400">{skill.proficiency}%</span>
      </div>
      <div className="h-2 bg-white/5 rounded-full overflow-hidden">
        <motion.div
          className="h-full bg-gradient-to-r from-cyan-500 to-cyan-300 rounded-full"
          initial={{ width: 0 }}
          animate={isInView ? { width: `${skill.proficiency}%` } : { width: 0 }}
          transition={{ duration: 1.2, delay, ease: [0.25, 0.46, 0.45, 0.94] }}
        />
      </div>
    </motion.div>
  )
}

export function SkillsSection() {
  const ref = useRef(null)
  const isInView = useInView(ref, { amount: 0.2, margin: "-50px" })

  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        staggerChildren: 0.1,
        delayChildren: 0.15,
      },
    },
  }

  const cardVariants = {
    hidden: { opacity: 0, y: 40 },
    visible: {
      opacity: 1,
      y: 0,
      transition: { duration: 0.5, ease: [0.25, 0.46, 0.45, 0.94] },
    },
  }

  return (
    <SectionWrapper id="skills" className="relative py-32 px-6">
      <div className="max-w-7xl mx-auto" ref={ref}>
        <motion.div
          initial={{ opacity: 0, y: 40 }}
          animate={isInView ? { opacity: 1, y: 0 } : { opacity: 0, y: 30 }}
          transition={{ duration: 0.6 }}
          className="text-center mb-16"
        >
          <span className="text-cyan-400 font-mono text-sm tracking-widest uppercase">Expertise</span>
          <h2 className="text-4xl md:text-5xl font-bold mt-4 text-balance">
            Technical{" "}
            <span className="bg-gradient-to-r from-cyan-400 to-white bg-clip-text text-transparent">Proficiency</span>
          </h2>
          <p className="text-gray-400 mt-4 max-w-2xl mx-auto">
            A comprehensive overview of my technical skills and proficiency levels, continuously refined through
            hands-on projects and continuous learning.
          </p>
        </motion.div>

        <motion.div
          className="grid md:grid-cols-3 gap-8"
          variants={containerVariants}
          initial="hidden"
          animate={isInView ? "visible" : "hidden"}
        >
          {skillCategories.map((category, categoryIndex) => (
            <motion.div key={category.title} variants={cardVariants}>
              <GlassCard hover className="p-6 h-full">
                <h3 className="text-lg font-semibold text-white mb-6 pb-4 border-b border-white/10">
                  {category.title}
                </h3>
                <div className="space-y-6">
                  {category.skills.map((skill, skillIndex) => (
                    <SkillBar key={skill.name} skill={skill} delay={categoryIndex * 0.1 + skillIndex * 0.1} />
                  ))}
                </div>
              </GlassCard>
            </motion.div>
          ))}
        </motion.div>
      </div>
    </SectionWrapper>
  )
}
